




































































import {Component, Prop, Provide, Vue, Watch} from 'vue-property-decorator';
import {FdTranslator} from '@/FdTranslator';
import {CrudService} from "@/services/CrudService";
import {Util} from "@/Util";


declare let $: any;

@Component({
    name: 'Seo',
    filters: {
        _: function (value) {
            return FdTranslator._(value);
        }
    },
    components: {}
})

export default class SearchTable extends Vue {


    @Prop({default: 'search-table'})
    title: string;

    @Prop({default: ''})
    data_url: string;

    @Prop({default: null})
    callback: any;

    @Prop({default: null})
    filter: any;

    @Provide()
    items: any = [];

    @Provide()
    timeout: any = null;

    @Provide()
    search: string = '';

    @Provide()
    splashScreen: boolean = true;

    @Prop({default: 'search_content_table'})
    name_view_tables: string;

    @Provide()
    table_filter: {
        root?: number
    } = null;

    @Watch('search')
    onChangeSearch(newValue, oldValue) {
        console.log(newValue);
        let me = this;
        if (me.timeout) {
            clearTimeout(me.timeout);
        }
        me.timeout = setTimeout(function () {
            me.splashScreen = true;
            me.filter.query = newValue;
            me.load();
        }, 500)
    }


    mounted(): void {
        let me = this;
    }

    showSearch(): void {
        let me = this;
        $('#search_modal').on('shown.bs.modal', function () {
            $(this).unbind('shown.bs.modal');
            me.load();
        }).on('hide.bs.modal', function () {
            me.splashScreen = true;
        }).modal('show')
    }

    load(): void {
        let me = this;
        CrudService.post(Util.httpRoot + me.data_url, {
            filter: me.filter
        }).then((response: any) => {
            me.splashScreen = false;
            if (response.data.success) {
                me.items = response.data.items;
            }
        }).catch((response) => {
            Util.errorHandler(response);
        });
    }

    callback_function(item: any, $event: Event): void {
        let me = this;
        if (this.callback.fn) {
            this.callback.fn(item);
        }
        $('#search_modal').on('', function () {
            me.splashScreen = false;
        }).modal('hide');
    }
}
