




import Vue from 'vue';
import {Component, Provide, Watch} from 'vue-property-decorator'
import FormBuilder from '@/components/form/FormBuilder.vue';
import {Util} from "@/Util";
import {ConfigService} from "@/services/ConfigService";

declare let $: any;

@Component({
    name: 'HelpItem',
    components: {
        'form-manager': FormBuilder
    }
})

export default class HelpItem extends Vue {
    @Provide()
    id: number = null;

    @Provide()
    load: boolean = false;

    @Provide()
    item: any = {
        id: 0,
        data: {
            metadata: {}
        },
        properties: [],
        related: [],
        relatedChildren: []
    };

    @Watch('item', {deep: true})
    onChangeItem(newItem, oldItem) {
        if (this.load) {
            this.$store.dispatch('setRouteNotify', true);
        }
    }

    getItem(): void {
        let me = this;
        ConfigService.getHelpItem(this.id).then((response: any) => {
            if (response.data.success) {
                me.prepareResponse(response);
            } else {
                Util.errorHandler(response)
            }
        }, (response) => {
            Util.errorHandler(response)
        });
    }

    mounted(): void {
        let me = this;
        this.id = parseInt(this.$route.params.id);
        if (this.$route.params.id == 'new') {
            this.id = 0;
        }
        me.getItem();
    }

    prepareResponse(response: any): void {
        let me = this;
        me.$store.dispatch('setBreadcrumbs', {
            items: response.data.breadcrumbs,
            page_title: response.data.page_title
        });
        me.$set(me, 'item', response.data.items[0]);// <-- Обновляемый объект

        let url = me.item.id > 0 ? response.data.form.create_url : response.data.form.update_url;

        if (response.data.notifications) {
            me.$store.dispatch('setNotifications', response.data.notifications);
        }

        me.$store.dispatch('setForm', {// <-- ставим форму в хранилище
            name: 'help-item',
            help: response.data.form.help,
            content: {
                buttons: Util.buttons([
                    Util.buttonSave({
                        me: me,
                        url: url,
                        item: me.item,
                        callback: function (response) {
                            me.getItem();
                        },
                        route_name: 'emails_item'
                    }),
                    Util.buttonSaveAndClose({
                        me: me,
                        url: url,
                        item: me.item,
                        route_name: 'help_items'
                    }),
                    Util.buttonUpdate({
                        callback: function () {
                            me.update();
                        }
                    }),
                    Util.buttonDelete('config/helps/update', me.item)
                ]),
                tabs: response.data.form.tabs
            }
        });

        me.$nextTick(function () {
            $('.dropdown-toggle').dropdown();
            $('.tooltip-container').tooltip({
                selector: '[data-toggle=tooltip]',
                container: 'body'
            });
        })
    }

    update(): void {
        let me = this;
        me.$store.dispatch('setRouteNotify', false);
        me.$store.dispatch('setBreadcrumbs', {
            items: []
        });
        me.$set(me, 'item', {});
        me.$store.dispatch('clearForm');
        me.getItem();
    }
}
